<template>
  <div class="user-chat">
    <van-nav-bar
      title="QQ"
      class="app-nav-bar"
      left-arrow
      @click-left="$router.back()"
    />
    <van-list
      class="messageList"
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
      <van-cell v-for="item in list" :key="item" :title="item" />
    </van-list>
    <!--  -->
    <van-cell-group class="sendMessage">
      <van-field v-model="message" placeholder="" :border="false" />
      <van-button type="primary"></van-button>
    </van-cell-group>
  </div>
</template>

<script >
//  socket.io-client
import io from 'socket.io-client'

// 
const socket = io('http://ttapi.research.itcast.cn')

socket.on('connect', function () {
  console.log('')
})
socket.on('event', function (data) {})
socket.on('disconnect', function () {
  console.log('')
})
export default {
  name: 'UserChat',
  props: {},
  components: {},
  data() {
    return {
      message: '',
      list: [],
      loading: false,
      finished: false
    }
  },
  computed: {},
  watch: {},
  methods: {
    onLoad() {
      // 
      // setTimeout  ajax 
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1)
        }

        // 
        this.loading = false

        // 
        if (this.list.length >= 10) {
          this.finished = true
        }
      }, 1000)
    }
  },
  beforeCreate() {},
  // -this
  created() {},
  beforeMount() {},
  // -DOM
  mounted() {},
  beforeUpdate() {},
  // -
  updated() {},
  // keep-alive
  activated() {},
  // keep-alive
  deactivated() {},
  // -,
  beforeDestroy() {},
  // -
  destroyed() {}
}
</script>
<style lang='less' scoped>
/* @import url(); css */
.user-chat {
  .app-nav-bar {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
  }
  .messageList {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 60px;
    top: 50px;
    overflow-y: auto;
  }
  .sendMessage {
    display: flex;
    padding: 10px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
  }
}
</style>